import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';


class MenuDisplay extends Component {
    orderId = []
    placeOrder  = (id) => {
        this.orderId.push(`${id}`)
        this.props.finalOrder(this.orderId)
    }

    removeOrder = (id) => {
        /* console.log(id)
        console.log(this.orderId)
        console.log(this.orderId.indexOf(id))*/
        this.orderId.splice(this.orderId.indexOf(id.toString()), 1)
        this.props.finalOrder(this.orderId)
    }
/*
    orderId = []
    placeOrder = (id) => {
        this.orderId.push(`${id}`)
        this.props.finalOrder(this.orderId)
    }
*/
    renderMenu = ({menudata}) => {
        if(menudata){
            return menudata.map((item) => {
                return(
                    <div key={item.course_id}>
                        <div className="col-md-7">
                            <b>{item.course_id}</b>&nbsp;
                            <img src={item.course_image} alt={item.course_name} style={{height:80,width:80,}}/>
                            &nbsp;&nbsp; {item.course_name} - {item.course_price}
                        </div>
                        <div className="col-md-4">
                            <button className="btn btn-primary" onClick={() => {this.placeOrder(item.course_id)}}>
                                <span className="glyphicon glyphicon-plus">Choose</span>
                            </button>&nbsp;
                            <button className="btn btn-warning" onClick={() => {this.removeOrder(item.course_id)}}>
                                <span className="glyphicon glyphicon-minus">Remove</span>
                            </button>
                        </div>
                        <br/>
                    </div>


                )
            })
        }
    }

    renderCart = (orders) => {
        if(orders){
            return orders.map((item,index) => {
                return (
                    <b key= {index}> {item} &nbsp;&nbsp;</b>
                )
            })
        }
    }

    render(){
        // console.log(">>>>menu",this.props)
        return(
            <div>
                <div className="col-md-12 bg-info">
                    <h1>CourseAvailability</h1>
                    Course Number {this.renderCart(this.orderId)} added
                </div>
                <div className="col-md-12 bg-info">
                    {this.renderMenu(this.props)}
                </div>
            </div>
        )
    }
}

export default withRouter(MenuDisplay);

