import {Link, withRouter } from 'react-router-dom';
import React,{Component} from 'react';
import './Search.css'

const locationUrl="https://uoiapi.herokuapp.com/location"
const collegeUrl = "https://uoiapi.herokuapp.com/college?stateId="

//const locationUrl ="https://zomatojulapi.herokuapp.com/location";


class Search extends Component {
    constructor(props) {
        super(props);

        this.state = {
            location :'',
            college:''
        }
    }
    
    renderCity = (data) =>{
        if(data){
            return data.map((item) =>{
                return(
                    <option value={item.state_id} key={item.state_id}>{item.state_name}</option>
                )
            })
        }
    }
    
    renderColleges = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.college_id} >{item.college_name}</option>
                )
            })
        }
    }

    handleCity = (event) =>{
        console.log(event.target.value)
        const stateId = event.target.value;
        fetch(`${collegeUrl}${stateId}`,{method: 'GET'})
        .then((res) =>res.json())
        .then((data) =>{
            this.setState({college:data})
        })
    }

    handleClg = (event) => {
        this.props.history.push(`/details/${event.target.value}`)
    }
    render() {
        console.log(this.state.college)
        return (
            <div id="search">
                <div id="login">
                    <div class="col-lg-6 col-md-6 col-sm-6 col-xs-6" id="#logoofweb1">
                        <img src="https://i.ibb.co/8gkKfvF/hp61.png"/>
                    </div>
                    <div class="col-lg-6 col-md-6 col-sm-6 col-xs-6" id="#logoofweb2">
                        <a href="#" id="loginsignuplocated1">
                            Login
                        </a>&nbsp;
                        <a href="#"  id="loginsignuplocated2">
                        Signup - it's free
                        </a>
                    </div>
                </div>
                
                <div id="heading">
                    Be discovered by the best
                    universities in the Bharath <span id="usercity"></span>
                </div>
                <div class="signupbuttoncenter">
                    <button>
                        Signup - it's free
                    </button>
                </div>
                <div id="dropdown">
                    <select id="city" onChange={this.handleCity}>
                        <option>---SELECT LOCATION---</option>
                        {this.renderCity(this.state.location)}
                    </select>
                    <select id="restaurants" onChange={this.handleClg}>
                        {this.renderColleges(this.state.college)}
                    </select>
                    
                </div>
            </div>
        )
    }
    //on page load we have to call api
    componentDidMount() {
        fetch(locationUrl,{method:'GET'})
        .then((res) =>res.json())
        .then((data) =>{
            this.setState({location: data})
        })
    }
}

export default withRouter(Search);


