import React, {Component} from 'react';
import axios from 'axios';
import './Filter.css'
//


const  url ="https://zomatojulapi.herokuapp.com/filter"

class cuisineFilter extends Component {
    filterCuisine = (event) => {
        let mealId = sessionStorage.getItem('mealId')
        let cuisineId = event.target.value;

        let filterUrl;
        if(cuisineId == ""){
            filterUrl =`${url}/${mealId}`
        }else{
            filterUrl =`${url}/${mealId}?cuisine=${cuisineId}`
        }
        axios.get(filterUrl)
        .then((res) =>{this.props.restPerCuisine(res.data)})
    }
    render(){
        return (
            < div className="cuisineFilter">
                <h3> 
                    Stages Filter   
                </h3> 
                <div onChange={this.filterCuisine}>
                    <label className="radio">
                        <input type="radio" name="stages" value=""/>All
                    </label>
                    <label className="radio">
                        <input type="radio" name="stages" value="1"/>Bachlors
                    </label>
                    <label className="radio">
                        <input type="radio" name="stages" value="2"/>Masters
                    </label>
                    <label className="radio">
                        <input type="radio" name="stages" value="3"/>Distance
                    </label>
                </div>
            </div>
        )
    }
}

export default cuisineFilter;