import React from 'react';
import {Link} from 'react-router-dom';
import './listing.css';
const listingDisplay =(props) => {
    
    const renderList =({listData}) =>{
        if(listData){
            return listData.map((item) =>{
                return(
                    <div >
                        <div className="item" key={item._id}>
                            
                            <div className="row">
                                <div className="col-md-5 col-sm-5 col-xs-5">
                                    <img className="Image" src={item.college_thumb}/>
                                </div>
                                <div className="col-md-7 col-sm-7 col-xs-7">
                                    <div className="hotel_name">
                                        <Link to={`/details/${item.college_id}`}>{item.college_name}</Link>
                                        <div className="city_name">{item.address}</div>
                                        <div className="city_name rating">{item.rating_text}</div>
                                        <div className="city_name cost">Rs.{item.cost}</div>
                                        <br/>
                                        <div>
                                            <span className="label label-primary">
                                                {item.studyTypes[0].studytype}
                                            </span> &nbsp;
                                            <span className="label label-info">
                                                {item.studyTypes[1].studytype}
                                            </span>&nbsp;
                                            
                                        </div>
                                        <div>
                                            <span className="label label-warning">
                                                {item.stages[0].stage_name}
                                            </span> &nbsp;
                                            <span className="label label-info">
                                                {item.stages[1].stage_name}
                                            </span>&nbsp;
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                    </div>
                )
            })
        }
        else {
            return(
                <div >
                    <img src="https://i.ibb.co/2sPXhkJ/loading2.gif"/>
                    <h3>loading...</h3>
                </div>
            ) 
        }
    }
    return(
        <div id="content">
            {renderList(props)}
        </div>
    )
}

export default listingDisplay;