import React,{Component} from 'react';
import axios from 'axios';
import ListingDisplay from './listingDisplay';
import CuisineFilter from  '../filter/CuisineFilter';
import CostFilter from '../filter/CostFilter';
import Header from '../Header';
import './listing.css'

const url = "https://univercityapi.herokuapp.com/college?studytype_id="


class Listing extends Component {
    constructor() {
        super()


        this.state = {
            collegeList:''
        }
    }

    setDataAsPerFilter = (sortedData) => {
        this.setState({restaurantList:sortedData})

    }
    render() {
        return (
            <div className="row">
                <Header/>
                <div id="mainListing">
                    <div id="filter">
                        <center>
                            <h3>
                                Filter
                            </h3>
                            <hr/>
                        </center>
                        <CuisineFilter restPerCuisine={(data) => {this.setDataAsPerFilter(data)}}/>
                        <hr/>
                        <CostFilter restPerCost={(data) => {this.setDataAsPerFilter(data)}}/>
                        <hr/>
                        
                    </div>
                    <ListingDisplay listData={this.state. collegeList}/>
                </div>
            </div>
        )
    }

    //callapi
    componentDidMount() {
        let studyId = this.props.match.params.studyId;
        sessionStorage.setItem('studyId',studyId)
        axios.get(`${url}${studyId}`)
        .then((res) =>{this.setState({collegeList:res.data})})
    }
}

export default Listing;

