import React from 'react';
import {Link} from 'react-router-dom';
import './QuickSearch.css'

const QuickSearch = (props) => {

    
    const listStudy = ({quickData}) =>{
        if(quickData){
            return quickData.map((item) =>{
                return(
                    <Link to={`/list/${item.studytype_id}`}>
                        <div class="tileContainer" key={item.studytype_id}>
                            <div class="tileComponent1">
                                <img src={item.study_image} alt="breakfast"/>
                            </div>
                            <div class="tileComponent2">
                                <div class="componentHeading">
                                    {item.studytype}
                                </div>
                                <div class="componentSubHeading">
                                    {item.content}
                                </div>
                            </div>
                        </div>
                    </Link>
                )
            })
        }
    }


    
    return(
        <div id="quicksearch">
        <span id="QuickHeading">
            Quick Choose
        </span>
        <span id="QuickSubHeading">
            Discover Universitys by type of Studys
        </span>
        <div id="mainTileContainer">
            {listStudy(props)}
        </div> 
    </div>
    )
}

export default QuickSearch;