import React, {Component} from 'react';
import axios from 'axios';
import './Filter.css'
//


const  url ="https://uoiapi.herokuapp.com/filter"

class CostFilter extends Component {

    filterCost = (event) => {
        let mealId = sessionStorage.getItem('mealId')
        let cost = (event.target.value).split('-');
        let lcost = cost[0];
        let hcost = cost[1];
        let filterUrl;
        if(event.target.value == ""){
            filterUrl =`${url}/${mealId}`
        }else{
            filterUrl =`${url}/${mealId}?lcost=${lcost}&hcost=${hcost}`
        }
        axios.get(filterUrl)
        .then((res) =>{this.props.restPerCost(res.data)})
    }
    render(){
        return (
            < div className="cuisineFilter">
                <h3>
                    Cost Filter   
                </h3> 
                <div onChange={this.filterCost}>
                    <label className="radio">
                        <input type="radio" name="cuisine" value=""/>All
                    </label>
                    <label className="radio">
                        <input type="radio" name="cuisine" value="10000-20000"/>1000-2000
                    </label>
                    <label className="radio">
                        <input type="radio" name="cuisine" value="20000-30000"/>2000-3000
                    </label>
                    <label className="radio">
                        <input type="radio" name="cuisine" value="30000-40000"/>3000-40000
                    </label>
                    <label className="radio">
                        <input type="radio" name="cuisine" value="40000-50000"/>1000-1500
                    </label>
                
                </div>
            </div>
        )
    }
}

export default CostFilter;